import * as React from 'react';
import * as styles from '../Navigation.module.scss';
import { Instagram } from '~src/assets/icons/social/instagram';
import { LinkedIn } from '~src/assets/icons/social/linkedin';
import { Youtube } from '~src/assets/icons/social/youtube';
import { Artstation } from '~src/assets/icons/social/artstation';
import { useStaticQuery, graphql } from 'gatsby';
const SocialLinks = ()=>{
    const data = useStaticQuery(QUERY);
    return React.createElement(React.Fragment, null, data.allSocialLinksYaml.nodes.map((item)=>{
        switch(item.label){
            case 'Instagram':
                return React.createElement("a", {
                    className: `${styles.socialLink} ${styles.socialInstagram}`,
                    href: item.url,
                    title: "Instagram",
                    target: "_blank",
                    rel: "noopener noreferrer"
                }, "Instagram", React.createElement(Instagram, null));
            case 'LinkedIn':
                return React.createElement("a", {
                    className: `${styles.socialLink} ${styles.socialLinkedIn}`,
                    href: item.url,
                    title: "LinkedIn",
                    target: "_blank",
                    rel: "noopener noreferrer"
                }, "LinkedIn", React.createElement(LinkedIn, null));
            case 'Artstation':
                return React.createElement("a", {
                    className: `${styles.socialLink} ${styles.socialArtstation}`,
                    href: item.url,
                    title: "ArtStation",
                    target: "_blank",
                    rel: "noopener noreferrer"
                }, "Artstation", React.createElement(Artstation, null));
            case 'YouTube':
                return React.createElement("a", {
                    className: `${styles.socialLink} ${styles.socialYoutube}`,
                    href: item.url,
                    title: "YouTube",
                    target: "_blank",
                    rel: "noopener noreferrer"
                }, "YouTube", React.createElement(Youtube, null));
            default:
                break;
        }
    }));
};
export default SocialLinks;
const QUERY = graphql`
	query SocialLinkQuery {
		allSocialLinksYaml {
			nodes {
				url
				label
			}
		}
	}
`;
