import * as React from 'react';
export function useStickynav(stickyRef, isPost, activeClass) {
    const [currentClass, setCurrentClass] = React.useState(null);
    let lastScrollY = typeof window !== `undefined` && window.scrollY;
    let ticking = false;
    const update = ()=>{
        if (stickyRef && stickyRef.current !== null) {
            const top = stickyRef.current.getBoundingClientRect().top;
            const trigger = top + window.scrollY;
            let triggerOffset = -20;
            if (isPost) {
                triggerOffset = stickyRef.current.offsetHeight + 35;
            }
            if (lastScrollY >= trigger + triggerOffset) {
                setCurrentClass(activeClass);
            } else {
                setCurrentClass('');
            }
            ticking = false;
        }
    };
    const requestTick = ()=>{
        if (!ticking) {
            requestAnimationFrame(update);
        }
        ticking = true;
    };
    const onScroll = ()=>{
        lastScrollY = window.scrollY;
        requestTick();
    };
    const throttledScroll = ()=>setTimeout(onScroll, 50);
    React.useEffect(()=>{
        window.addEventListener('scroll', throttledScroll, {
            passive: true
        });
        return ()=>{
            window.removeEventListener('scroll', ()=>throttledScroll);
        };
    }, []);
    return currentClass;
}
