export function getMetadata(data, template, site) {
    if (template === 'home') {
        const seo = {
            title: site.title,
            desc: site.desc,
            image: site.image
        };
        return seo;
    }
    if (template === 'notfound') {
        const seo1 = {
            title: site.notFound,
            desc: site.desc,
            image: site.image
        };
        return seo1;
    }
    if (template === 'page') {
        const seo2 = {
            title: data.page.frontmatter.title,
            desc: data.page.excerpt,
            image: site.image
        };
        return seo2;
    }
    if (template === 'post') {
        const seo3 = {
            title: data.post.frontmatter.title,
            desc: data.post.excerpt,
            image: `${site.url}${data.post.frontmatter.image.childImageSharp.gatsbyImageData.images.fallback.src}`,
            date: data.post.frontmatter.date,
            author: site.title
        };
        return seo3;
    }
    if (template === 'tag') {
        const seo4 = {
            title: data.tag.id,
            desc: data.tag.id,
            image: `${site.url}${data.tag.image.childImageSharp.gatsbyImageData.images.fallback.src}`
        };
        return seo4;
    } else {
        const seo5 = {
            title: site.title,
            desc: site.desc,
            image: site.image
        };
        return seo5;
    }
}
